import React from 'react';
import { AdvancedMarker } from '@vis.gl/react-google-maps';
import Image from '../../images';
import './Marker.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLocationDot } from '@fortawesome/free-solid-svg-icons';

const Marker = ({ style, position, active, onToggle, type }) => {



  return (
    <AdvancedMarker
      position={position}
      className={`marker ${active ? 'active' : ''}`}
      zIndex={active ? 2 : 1}
      onClick={onToggle}
    >
      {type === 'Pin' ?
        <FontAwesomeIcon icon={faLocationDot} style={{ color: 'green', width: '30px', height: '30px' }} /> :
        (type === 'ThreeDot' || type === 'DRIVE' || type === 'TWO_WHEELER' || type === 'OriginMarker' || type === 'DestinationMarker') ? <Image type={type} style={style} /> :
          <img alt="lg" src={type} className={'customPin'} />}
    </AdvancedMarker>
  );
};

export default Marker;
