import React, { useMemo, useState } from 'react';
import { useMap, Map as Gmap, InfoWindow } from '@vis.gl/react-google-maps';
import { DEFAULT_MAP_CENTER, DEFAULT_MAP_ZOOM } from '../../constants';
import Marker from './Marker';
import Polyline, { InfoWindow as PolylineInfoWindow } from './Polyline';
import MapHandler from './MapHandler';

const Map = ({ origin, destination, travelMode, routes, currentLocation, pins = [], controls, zoom }) => {
  const map = useMap();
  const [infoWindow, setInfoWindow] = useState(null);
  const [activeRoute, setActiveRoute] = useState(0);

  const stops = useMemo(() => {
    let stopsArray = [];
    if (origin?.position) stopsArray.push(origin);
    if (pins) pins?.map(pi=>stopsArray.push(pi))
    if (destination?.position) stopsArray.push(destination);
    
    return stopsArray;
  }, [origin, destination, pins]);

  const handlePolylineClick = (event, route, routeIndex) => {
    const position = {
      lat: event.latLng.lat(),
      lng: event.latLng.lng(),
    };
    setActiveRoute(routeIndex);
    setInfoWindow({
      position,
      content: <PolylineInfoWindow route={route} travelMode={travelMode} />,
    });
  };

  return (
    <Gmap
      mapId={'FLOWHEROMAPS'}
      defaultZoom={DEFAULT_MAP_ZOOM}
      defaultCenter={(currentLocation ?? DEFAULT_MAP_CENTER)}
      center={pins?.length === 1 ? { ...pins[0].position } : null}
      zoom={pins?.length === 1 ? zoom : null}
      gestureHandling="greedy"
      disableDefaultUI={false}
    >
      <MapHandler map={map} stops={stops} />
      {origin && <Marker type="OriginMarker" position={origin.position} />}
      {destination && <Marker type="DestinationMarker" position={destination.position} />}
      {pins?.length > 0 && pins?.map((pin, index) => <Marker key={index} type={pin?.type ? pin?.type : "Pin"} position={pin.position} />)}
      {currentLocation?.icon && <Marker type={currentLocation?.icon} position={currentLocation} />}
      {routes.map((route, routeIndex) => (
        <Polyline
          key={`polyline-${routeIndex}`}
          route={route}
          activeRoute={activeRoute}
          routeIndex={routeIndex}
          onClick={handlePolylineClick}
        />
      ))}

      {infoWindow && (
        <InfoWindow
          key={infoWindow.position.lat + ',' + infoWindow.position.lng}
          position={infoWindow.position}
          onCloseClick={() => setInfoWindow(null)}
        >
          {infoWindow.content}
        </InfoWindow>
      )}
    </Gmap>
  );
};

export default React.memo(Map);
