import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import SearchBox from '../components/SearchBox';
import { getDirections } from '../services/google';
import { TRAVEL_MODES } from '../constants';
import { setMapData } from '../../../../../reduxStore/actions/flowActions';

const SearchBoxContainer = () => {
  const { map, companyData } = useSelector(state => state.flowReducer);
  const dispatch = useDispatch();

  useEffect(() => {
    const fetchDirections = async () => {
      if (map?.origin && map?.destination && Object.keys(map?.origin)?.length && Object.keys(map?.destination)?.length) {
        try {
          const newRoutes = {};
          for (const mode of TRAVEL_MODES) {
            const res = await getDirections({
              origin: { address: map?.origin.formattedAddress },
              destination: { address: map?.destination.formattedAddress },
              travelMode: mode,
              routingPreference: 'traffic_aware',
              extraComputations: ['TRAFFIC_ON_POLYLINE'],
            }, companyData?.misc_info?.map);
            newRoutes[mode] = res?.routes || [];
          }
          dispatch(setMapData({ routes: newRoutes }))
        } catch (error) {
          dispatch(setMapData({ routes: [], error: 'Failed to fetch directions' }));

        }
      }
    };

    fetchDirections();
  }, [map?.origin, map?.destination, dispatch]);

  const handleAddressChange = (type, address) => {
    dispatch(
      setMapData({
        [type]: {
          name: address.name,
          formattedAddress: address.formatted_address,
          position: {
            lat: address.geometry.location.lat(),
            lng: address.geometry.location.lng(),
          },
          place_id: address.place_id,
        },
      }),
    );
  };

  return (
    <SearchBox
      travelMode={map?.travelMode}
      routes={map?.routes}
      onTravelModeChange={(mode) => dispatch(setMapData({ travelMode: mode }))}
      onOriginAddressChange={(address) => handleAddressChange('origin', address)}
      onDestinationAddressChange={(address) => handleAddressChange('destination', address)}
    />
  );
};

export default SearchBoxContainer;
