import axios from 'axios';

const DIRECTION_API_URL = 'https://routes.googleapis.com/directions/v2:computeRoutes';

const buildRequestBody = (body) => ({
  ...body,
  polylineQuality: 'high_quality',
  computeAlternativeRoutes: true,
});

const getDirections = async (body, apikey = '') => {
  try {
    const response = await axios.post(DIRECTION_API_URL, buildRequestBody(body), {
      headers: {
        'x-goog-api-key': apikey,
        'x-goog-fieldmask': 'routes.duration,routes.distanceMeters,routes.polyline.encodedPolyline,routes.optimizedIntermediateWaypointIndex,routes.travelAdvisory.speedReadingIntervals',
      },
    });
    return response.data;
  } catch (error) {
    console.error('Error fetching directions:', error);
    throw error;
  }
};

export { getDirections };
